@import url(https://fonts.googleapis.com/css?family=Tangerine);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.setup {
  background-color: rgb(208, 200, 214);
  width: 100%;
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #4e43e4;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: muli;
}
.navbar {
  background-color: rgb(218, 91, 91);
}
.navbar-brand {
  font-size: 1.9em;
  color: #4e43e4;
  
}
.navbar a:hover {
  color: #4e43e4;
}
@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.5em;
  }
}
#header {
  width: 100%;
  height: 100%;
  font-weight: 70%;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header.brand-name {
  color: aqua;
}
#header h2 {
  color: #4e43e4;
  font-size: 24px;
}
#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: blue;
  border: 2px solid black;
  text-decoration: none;
}
#header .btn-get-started:hover {
  background-color: blue;
  color: #fff;
}
.header-img {
  text-align: center;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
.text-center {
  color: blue;
}

.card {
  box-shadow: 5 12px 12px 5 rgba(8, 8, 8, 0.993);
  padding: 16px;
  text-align: center;
  background-color: #e4d1e6;
}
@media screen and (max-width: 600px) {
  .col-4 {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}
p{
margin-left: 10px;
margin-right: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

